<template>
  <MessageInput @addMessage="addMessage" />
  <ListContainer
    :messages="messages"
    :completeAllItems="completeAllItems"
    :completeSingleItem="completeSingleItem"
    :deleteSingleItem="deleteSingleItem"
    :openEditMode="openEditMode"
    :closeEditMode="closeEditMode"
    :isAllCompleted="isAllCompleted"
  />
  <footer class="footer" v-if="this.messages.length !== 0">
    <span class="todo-count"
      ><strong>{{ this.ActiveMessage.length }}</strong> item left</span
    >
    <!-- <div id="nav">
      <ul class="filters">
        <li>
          <a href="#/" :class="{ selected: this.inWhichPage === 'All' }">All</a>
        </li>

        <li><a href="#/active" :class="{ selected: this.inWhichPage === 'Active' }">Active</a></li>
        <li>
          <a href="#/completed" :class="{ selected: this.inWhichPage === 'Completed' }"
            >Completed</a
          >
        </li>
      </ul>
    </div> -->
    <div id="nav">
      <ul class="filters">
        <li v-for="(message, index) in messages" :key="message.id">
          <a
            href="#"
            @click="changePage(index)"
            :class="{ selected: this.pageNum === index + 1 }"
            >{{ index + 1 }}</a
          >
        </li>
      </ul>
    </div>
    <button v-if="hasCompleted" class="clear-completed" @click="deleteCompletedItem">
      Clear completed
    </button>
  </footer>
</template>

<script>
import MessageInput from '@/components/MessageInput.vue';
import ListContainer from '@/components/ListContainer.vue';

export default {
  name: 'TodoApp',
  data() {
    return {
      messages: [],
      pageNum: 1,
    };
  },
  methods: {
    changePage(index) {
      this.pageNum = index + 1;
    },
    addMessage(temp) {
      this.messages.push(temp);
    },
    completeAllItems() {
      if (this.isAllCompleted === true) {
        this.messages.forEach((item) => {
          item.isCompleted = false;
        });
      } else {
        this.messages.forEach((item) => {
          item.isCompleted = true;
        });
      }
    },
    findMessage(id) {
      return this.messages.find((message) => message.id === id);
    },
    completeSingleItem(id) {
      this.findMessage(id).isCompleted = !this.findMessage(id).isCompleted;
    },
    deleteSingleItem(id) {
      this.messages.splice(this.findMessage(id), 1);
    },
    deleteCompletedItem() {
      this.messages = this.ActiveMessage;
    },
    openEditMode(id) {
      this.findMessage(id).isEditing = true;
    },
    closeEditMode(id) {
      this.findMessage(id).isEditing = false;
      if (!this.findMessage(id).text) {
        this.deleteSingleItem(id);
      }
    },
  },
  watch: {
    messages: {
      handler(newValue) {
        const parsed = JSON.stringify(newValue);
        localStorage.setItem('messages', parsed);
      },
      deep: true,
    },
  },
  computed: {
    ActiveMessage() {
      return this.messages.filter((message) => message.isCompleted === false);
    },
    isAllCompleted() {
      return this.messages.every((message) => message.isCompleted === true);
    },
    hasCompleted() {
      return this.messages.some((message) => message.isCompleted === true);
    },
    inWhichPage() {
      if (this.$route.path === '/active') {
        return 'Active';
      }
      if (this.$route.path === '/completed') {
        return 'Completed';
      }
      return 'All';
    },
  },
  mounted() {
    if (localStorage.getItem('messages') !== null) {
      this.messages = JSON.parse(localStorage.getItem('messages'));
    } else {
      this.messages = [];
    }
  },
  components: {
    MessageInput,
    ListContainer,
  },
};
</script>
