<template>
  <li
    v-for="(message, index) in filteredMessages"
    :key="message.id"
    :class="{ completed: message.isCompleted, editing: message.isEditing }"
  >
    <div class="view">
      <input
        class="toggle"
        :checked="message.isCompleted"
        type="checkbox"
        @click="completeSingleItem(message.id)"
      />
      <label @dblclick="openEditMode(message.id)"> {{ index + 1 }}. {{ message.text }}</label>
      <button class="destroy" @click="deleteSingleItem(message.id)"></button>
    </div>

    <input
      class="edit"
      v-model="message.text"
      @keyup.enter="closeEditMode(message.id)"
      @blur="closeEditMode(message.id)"
    />
  </li>
</template>

<script>
export default {
  name: 'SingleItem',
  props: [
    'filteredMessages',
    'completeSingleItem',
    'deleteSingleItem',
    'openEditMode',
    'closeEditMode',
  ],
};
</script>
