<template>
  <input
    class="new-todo"
    placeholder="What needs to be done?"
    autofocus
    v-model="this.temp"
    @keyup.enter="addMessage"
  />
</template>

<script>
import { v4 as uuidv4 } from 'uuid';

export default {
  name: 'AddItem',
  emits: {
    addMessage: (item) => typeof item === 'object',
  },
  data() {
    return {
      temp: '',
    };
  },
  methods: {
    addMessage() {
      if (this.message.text !== '') {
        this.$emit('addMessage', this.message);
        this.temp = '';
      }
    },
  },
  computed: {
    message() {
      return {
        id: uuidv4(),
        text: this.temp,
        isCompleted: false,
        isEditing: false,
      };
    },
  },
};
</script>
